				{"id":2,"question":"回收机支持哪些物品投递?","answer":"目前小红心支持以下类型回收：饮料瓶、纺织物、废旧金属、废塑料、纸类、有害物品（有害物品为公益回收，不产生收益），具体品类以回收机实际情况为准。"},
